import { get, post, put } from '../helpers/request.js';
import { publish, subscribe } from '../helpers/pubSub.js';

/**
 * A highscore which contains a url and currentEntries of previous winners
 */
export default class HighScore {
  /**
   * @param {RequestInfo} url
   */
  constructor(url) {
    this.url = url;
    /** @type {import('../../../params').Entry[] | undefined} */
    this.currentEntries = undefined;
  }

  /**
   * @param {string} player
   */
  async updatePlayer(player) {
    const winner = this.currentEntries
      ? this.currentEntries.filter((entry) => entry.name === player)
      : undefined;
    if (winner) {
      const updatedWins = winner[0].wins + 1;
      const body = {
        wins: updatedWins,
      };
      await put(this.url, winner[0].dbId, body);
      await this.getNames();
    }
  }

  getWinner() {
    subscribe('Game:end-game', (event) => {
      const winner = event.detail.currentPlayer.name;
      if (this.currentEntries && this.currentEntries.find((entry) => entry.name === winner)) {
        this.updatePlayer(winner);
      } else {
        this.postPlayer(winner);
      }
    });
  }

  async bindEvents() {
    await this.getNames();
    this.getWinner();
  }

  async getNames() {
    this.currentEntries = await get(this.url);
    publish('HighScore:allscores', this.currentEntries);
  }

  /**
   * @param {string} player
   */
  async postPlayer(player) {
    const entry = {
      name: player,
      wins: 1,
    };
    try {
      await post(this.url, entry);
      await this.getNames();
    } catch (err) {
      // eslint-disable-next-line no-console
      console.error(err);
    }
  }
}
